<template>
  <div class="home">
    <el-container>
      <el-header>
        <!-- 表单 -->
        <el-form ref="addForm" :inline="true" :model="orderInfo" :rules="rules" class="demo-form-inline">
          <el-form-item prop="goods_name" label="商品名称">
            <el-input v-model="orderInfo.goods_name"></el-input>
          </el-form-item>
          <el-form-item prop="goods_price" label="价格">
            <el-input v-model="orderInfo.goods_price" type="number"></el-input>
          </el-form-item>
          <el-form-item prop="goods_num" label="数量">
            <el-input v-model="orderInfo.goods_num" type="number"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addOrder">加入购物车</el-button>
          </el-form-item>
        </el-form>
      </el-header>
      <el-main>
        <!-- 表格 -->

        <el-checkbox-group v-model="chosResult">
          <el-table :data="orderList" stripe style="width: 100%">
            <el-table-column width="140" label="编号">
              <template slot-scope="scoped">
                <el-checkbox :label="scoped.row.id"></el-checkbox>
              </template>
            </el-table-column>
            <el-table-column prop="goods_name" label="名称">
            </el-table-column>
            <el-table-column prop="goods_price" label="价格/元">
            </el-table-column>
            <el-table-column label="数量/个">
              <template slot-scope="scoped">
                <el-input-number v-model="scoped.row.goods_num" :min="1" label="描述文字">
                </el-input-number>
              </template>
            </el-table-column>
            <el-table-column label="小计/元">
              <template slot-scope="scoped">
                {{scoped.row.goods_price * scoped.row.goods_num}}
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scoped">
                <el-button type="danger" @click="delOrder(scoped.row.id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-checkbox-group>

      </el-main>
      <el-footer>
        <!-- 底栏 -->
        <div class="footer">
          <div class="foot-left">
            <el-checkbox-button v-model="qxFlag">全选</el-checkbox-button> &nbsp;
            <el-button type="danger" @click="delOrders">删除所选</el-button>
          </div>
          <div class="foot-right">总价：￥{{total}}</div>
        </div>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  data () {
    return {
      orderInfo: {},
      orderList: [{
        id: 1,
        goods_name: '桂林西瓜霜',
        goods_price: 12,
        goods_num: 12
      },
      {
        id: 2,
        goods_name: '惠普电脑',
        goods_price: 3,
        goods_num: 4231
      }, {
        id: 3,
        goods_name: '双黄连口服液',
        goods_price: 12,
        goods_num: 12
      },
      {
        id: 4,
        goods_name: '和其正凉茶',
        goods_price: 12,
        goods_num: 12
      }],
      rules: {
        goods_name: [{ required: true, message: '请输入购买商品', trigger: 'blur' }],
        goods_price: [{ required: true, message: '商品价格不能为空', trigger: 'blur' }],
        goods_num: [{ required: true, message: '商品数量不能为空', trigger: 'blur' }]
      },
      // 用户所选订单
      chosResult: []
    }
  },
  computed: {
    total () {
      let money = 0
      this.chosResult.forEach(v => {
        // 查找选中商品
        let cz = this.orderList.filter(item => item.id === v)
        let order = cz[0]
        // 累加小计
        money += (order.goods_num * order.goods_price)
      })
      return money
    },
    qxFlag: {
      get: function () {
        if (this.chosResult.length === this.orderList.length) {
          return true
        } else {
          return false
        }
      },
      set: function (newVal) {
        if (newVal) {
          let ids = []
          this.orderList.forEach(item => {
            ids.push(item.id)
          })
          this.chosResult = ids
        } else {
          this.chosResult = []
        }
      }
    }
  },
  watch: {
    orderList: {
      handler (oVal, nVal) {
        sessionStorage.setItem('orders', JSON.stringify(this.orderList))
      },
      deep: true
    },
    chosResult: {
      handler (oVal, nVal) {
        sessionStorage.setItem('choos', JSON.stringify(this.chosResult))
      },
      deep: true
    }
  },
  methods: {
    addOrder () {
      this.$refs['addForm'].validate((valid) => {
        if (!valid) return

        this.orderInfo.id = this.orderList.length + 1
        this.orderList.push(this.orderInfo)
        this.orderInfo = {}
      })
    },
    delOrder (id) {
      let i = this.orderList.findIndex(item => item.id === id)
      this.orderList.splice(i, 1)

      let index = this.chosResult.indexOf(id)
      this.chosResult.splice(index, 1)
    },
    delOrders () {
      this.chosResult.forEach(v => {
        let i = this.orderList.findIndex(item => item.id === v)
        this.orderList.splice(i, 1)
      })
      this.chosResult = []
    }
  },
  created () {
    let o = JSON.parse(sessionStorage.getItem('orders'))
    if (o != null) {
      this.orderList = o
    }
    let c = JSON.parse(sessionStorage.getItem('choos'))
    if (c != null) {
      this.chosResult = c
    }
  }
}
</script>

<style>
.home {
  height: 100%;
  background-color: #fafafa;
}
.el-container {
  height: 90%;
  text-align: center;
}

.footer {
  padding: 0 100px;
}

.foot-left {
  float: left;
}

.foot-right {
  float: right;
}
</style>
